body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.landing_background { 
        background: url(/react_portfolio/static/media/coding2.053e654c.jpg) no-repeat center center fixed;
        background-size: cover;
        height: 100vh;
}

.img_overlay {
    background: rgba(0, 0, 0, .6);
    height: 100vh;
}

.lp_text {
    color: white;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 90vh;
}

@media screen and (max-width: 760px) {
    .dropdown-menu {
        position: relative;
        z-index: 1;
    }

    .lp_text {
        position: absolute;
        padding: 0px 20px;
        font-size: 25px;
    }
}

.top_nav {
    font-size: 20px;
    margin: 40px;
    float: right;
}

.main_header {
    color: white;
}
.navbar {
   font-size: 20px;
   padding: 10px;
}

.navbar-brand {
    font-family: 'Kumar One Outline', cursive;
}

.nav-link {
    color: white !important;
}
.aboutme_wrapper {
    min-height: 100vh;
}

.about_heading {
    color: #3c3b7e;
    font-size: 50px;
    padding-top: 30px;
}

.about_cards {
    display: flex;
    flex-flow: row;
    justify-content: center;
}

.cards {
    margin-top: 50px !important;
    width: 475px;
    margin: auto;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),
            0 2px 4px 0 rgba(0,0,0,0.08);
}

.card-heading {
    color: #3c3b7e;
    text-align: center;
}

.card-content {
    color: #181691;
}

.bottom_text {
    color: #181691;
    margin: 0px 3%;
    margin-bottom: 30px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),
    0 2px 4px 0 rgba(0,0,0,0.08);
}

@media screen and (max-width: 1025px) {
    .about_cards {
        display: flex;
        flex-flow: row wrap;
    }
}




.carousel_wrapper {
    margin: 20px;
}

.carousel_heading {
    font-size: 20px;
    background-color: #010050;
}

.carousel-item {
    max-width: 400px;
    height: auto;
}

.carousel-caption {
    height: 80%;
    background-color: rgba(0, 0, 0, .8);
}

.carousel_link {
    background-color: rgba(0, 0, 0);
    margin: 0% 20%;
}

.carousel-control-prev-icon, .carousel-control-next-icon {
    background-color: rgba(0, 0, 0, .8);
}

@media screen and (max-width: 1400px) {
    .carousel-item {
        max-width: 100%;
        height: auto;
    }

    .carousel-caption {
        height: 80%;
        background-color: rgba(0, 0, 0, .8);
    }
}
.portfolio_wrapper {
    background-color: #010030;
    min-height: 60vh;
}

.main_carousel {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly !important;
}

.portfolio_heading {
    font-size: 50px;
    padding-top: 30px;
}

@media screen and (max-width: 1030px) {
    .main-carousel {
        flex-wrap: wrap;
    }

}
.App {
  font-family: 'Inconsolata', monospace;
}
